  li {
    list-style-type: none; } 
  
  .wrapper-main {
    display: flex;
    flex-direction: row;
    position: relative;
    flex-wrap: wrap;
    margin: 0 auto; }
  
  
  .wrapper-main > .work {
    transition: all .25s ease-in-out;
    position: relative;
    overflow: hidden; }
    .wrapper-main > .work:after {
      pointer-events: none;
      transition: all .25s ease-in-out;
      font-size: 2em;
      color: #fff;
      position: absolute;
      top: 40%;
      overflow: hidden;
      bottom: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
      display: none;
      opacity: 0; }
    .wrapper-main > .work:hover {
      transition: all .25s ease-in-out; }
      .wrapper-main > .work:hover:after {
        pointer-events: none;
        transition: all .25s ease-in-out;
        display: block;
        opacity: 1; }
  
  .about__pic {
    width: 200px; }
  
  .tagline {
    text-align: center;
    margin: 0 auto;
    margin-top: -1.5em;
    color: #fff;
    display: block; }
  
  .links {
    display: none;
    flex-direction: row;
    position: relative;
    flex-wrap: wrap;
    margin: 0 auto;
    margin-top: 3em;
    width: 100%;
    max-width: 600px;
    padding-left: 0;
    justify-content: space-around;
    text-align: center; }
  
  .header-index .links {
    display: flex; }

  
  .links li {
    flex: 50%;
    width: 50%;
    padding-top: 1.5em; }
    .links li a {
      font-size: 1.25em; }
  
  .links a:before {
    padding-right: .5em; }
  
  .project {
    margin-bottom: 4em; }
  
  .projectList {
    padding-left: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; }
    .projectList li {
      flex: 100%;
      line-height: 1.5; }
  
  .logo {
    margin-right: 1em; }
  
  .screencast {
    max-width: 100%; }
  
  .contactInfo {
    padding-left: 0; }
    .contactInfo li {
      line-height: 1.5; }
  
  .wireframe {
    max-width: 100%; }
    .wireframe img {
      display: block;
      max-width: 100%; }
  
  .screenshots {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between; }
    .screenshots .screenshot {
      flex: 1;
      width: 100%;
      margin-bottom: 3em; }
      .screenshots .screenshot img {
        -webkit-box-shadow: 0px 3px 9px 3px rgba(0, 0, 0, 0.15);
        -moz-box-shadow: 0px 3px 9px 3px rgba(0, 0, 0, 0.15);
        box-shadow: 0px 3px 9px 3px rgba(0, 0, 0, 0.15);
        display: block;
        }
    .screenshots .screenshot-files img {
      margin: 0; }
  
  .tech-list {
    display: flex;
    flex-direction: row;
    margin-left: 0;
    padding-left: 0;
    justify-content: space-around;
    flex-wrap: wrap; }
    .tech-list li {
      position: relative; }
      .tech-list li p {
        position: absolute;
        display: block;
        opacity: 0;
        margin: 0 auto;
        background-color: white;
        margin-top: 0;
        width: 100%;
        padding: .125em;
        transition: all .5s ease;
        text-align: center; }
      .tech-list li img:hover, .tech-list li img:active, .tech-list li img:focus {
        cursor: pointer; }
        .tech-list li img:hover + p, .tech-list li img:active + p, .tech-list li img:focus + p {
          transition: all .5s ease;
          opacity: 1;
          text-align: center;
          background-color: white;
          z-index: 2;
          padding: .125em; }
  
  .tech {
    position: relative;
    margin-left: .5em;
    margin-right: .5em;
    margin-bottom: 1em;
    flex: 1;
    height: 3em;
    vertical-align: middle; }
  
  .tech-express {
    height: 2em;
    padding-top: .25em; }
  
  .text {
    margin-bottom: 2em; }
  
  .caption {

    padding-right: 2em; }
  
  .code-link {
    line-height: 1.5em;
    margin-bottom: .5em;
    display: block;
    max-width: 100px; }
  
  .screenshots .screenshot-svg {
    box-shadow: 0px 2px 9px 6px rgba(0, 0, 0, 0); }
    .screenshots .screenshot-svg img {
      box-shadow: 0px 2px 9px 6px rgba(0, 0, 0, 0); }
    .screenshots .screenshot-svg .caption {
      text-align: center; }
  
  .link-caption {
    margin-top: 0; }
  
  .numbered-list {
    margin-top: .5em;
    list-style: disc;
    list-style-position: inside; }
    .numbered-list li {
      list-style-type: decimal;
      max-width: 600px; }
  
  .list-title {
    margin-bottom: 0; }
  
  .h1-link {
    text-decoration: none; }
  
  @media only screen and (min-width: 600px) {
    .wrapper.wrapper-max-width {
      max-width: 600px; }
  
    .header-index {
      padding-bottom: 4em; }
  
    .links li {
      flex: 25%;
      width: 25%;
      padding-top: 1.5em; }
  
    .work {
      flex: 50%;
      height: 22vh; } }
  @media only screen and (min-width: 823px) {
